'use client'

import {useState} from 'react'
import CIDR from 'ip-cidr'
import {Delimiter, EMPTY_STRING} from '@nyx/kr-common/constant/Constant'

function calculateCidrDetails(cidrInput: string) {
    try {
        return new CIDR(cidrInput)
    } catch (e) {
        return null // Handle invalid CIDR
    }
}

/**
 * https://github.com/ortexx/ip-cidr
 * https://mxtoolbox.com/subnetcalculator.aspx
 */
export default function KrIp() {
    const [cidr, setCidr] = useState<string>(EMPTY_STRING)
    const [ip, setIp] = useState<string>(EMPTY_STRING)
    const [calculatedCidr, setCalculatedCidr] = useState<any>(null)

    const handleCalculateButtonClick = () => {
        const result = calculateCidrDetails(cidr)
        setCalculatedCidr(result)
        setIp(result!.start())
    }

    const handleIpChange = (ip: string) => {
        return calculatedCidr.contains(ip)
    }

    return (
        <>
            <h1>CIDR Calculator</h1>
            <input
                type="text"
                placeholder="Enter CIDR (e.g,. 1.0.0.14/12)"
                value={cidr}
                onChange={(e) => setCidr(e.target.value)}
            />
            <button onClick={handleCalculateButtonClick}>Calculate</button>

            {calculatedCidr && (
                <div style={{marginTop: '20px'}}>
                    {/* CIDR Result */}
                    <h2>CIDR Result</h2>
                    <table style={{width: '100%', backgroundColor: '#f0f0f0'}}>
                        <tbody>
                        <tr>
                            <td><strong>Input IP</strong></td>
                            <td>{calculatedCidr.cidr}</td>
                            <td><strong>CIDR Range</strong></td>
                            <td style={{color: 'red'}}>{calculatedCidr.start() + Delimiter.SPACE_HYPHEN_SPACE + calculatedCidr.end()}</td>
                        </tr>
                        <tr>
                            <td><strong>Input IP (Long)</strong></td>
                            <td>{calculatedCidr.address.bigInteger()[0]}</td>
                            <td><strong>CIDR Range (Long)</strong></td>
                            <td>{calculatedCidr.start({type: 'bigInteger'}) + Delimiter.SPACE_HYPHEN_SPACE + calculatedCidr.end({type: 'bigInteger'})}</td>
                        </tr>
                        <tr>
                            <td><strong>Input IP (Hex)</strong></td>
                            <td>{calculatedCidr.address.toHex()}</td>
                            <td><strong>CIDR Range (Hex)</strong></td>
                            <td>{calculatedCidr.addressStart.toHex() + Delimiter.SPACE_HYPHEN_SPACE + calculatedCidr.addressEnd.toHex()}</td>
                        </tr>
                        </tbody>
                    </table>
                    <br/>
                    {/* CIDR Range */}
                    <h2>CIDR Range</h2>
                    <input
                        type="text"
                        placeholder="Enter IP"
                        value={ip}
                        onChange={(e) => setIp(e.target.value)}
                    />
                    <p>
                        The IP address {ip} is{Delimiter.SPACE}
                        {handleIpChange(ip) ?
                            <span style={{color: 'green'}}>inside</span> : <span style={{color: 'red'}}>outside</span>} of the range
                    </p>
                </div>
            )}
        </>
    )
}